import { MarkType } from '@markable/interfaces';
import { BaseMark } from './BaseMark';
export class Group extends BaseMark {
    constructor() {
        super(...arguments);
        this.markType = MarkType.Group;
    }
    encodeCustomProperties() {
        const { clip, cornerRadius } = this.props;
        return {
            clip,
            cornerRadius,
        };
    }
    addMark() {
        let node;
        this.api.mark(this.createMark().child(n => (node = n)));
        return node;
    }
    createMark() {
        const nodeBuilder = super.createMark();
        const { facetName, facetKey } = this.props;
        if (facetName || facetKey) {
            if (!facetName || !facetKey) {
                throw new Error('Both facetName and facetKey props must be defined to enable faceting');
            }
            nodeBuilder.facet({
                name: facetName,
                partitionOn: facetKey,
            });
        }
        return nodeBuilder;
    }
}
