import { scaleLinear } from 'd3-scale';
import { QuantitativeScale } from './QuantitativeScale';
export class LinearScale extends QuantitativeScale {
    constructor() {
        super(...arguments);
        this.defaultZero = true;
    }
    createScale(args) {
        const domain = this.getDomain(args);
        const range = this.getRange(args);
        const scale = scaleLinear()
            .domain(domain)
            .range(range);
        this.addCommonProperties(scale);
        return {
            [this.nameValue]: scale,
        };
    }
}
