import { AxisOrientation } from '@markable/interfaces';
export function crossValue(axis, thickness) {
    const domainWidth = axis.domainWidth;
    const { orient } = axis;
    let result = domainWidth;
    switch (orient) {
        case AxisOrientation.Bottom: {
            result = domainWidth / 2;
            break;
        }
        case AxisOrientation.Left: {
            result = thickness - domainWidth / 2;
            break;
        }
        case AxisOrientation.Right: {
            result = domainWidth / 2;
            break;
        }
        case AxisOrientation.Top: {
            result = thickness + domainWidth / 2;
            break;
        }
    }
    return result;
}
