import { LinearScale, LogScale, PowScale, SequantialScale, SqrtScale, TimeScale, UtcScale, } from './quantitative';
import { BandScale, OrdinalScale, PointScale } from './discrete';
export * from './quantitative';
export * from './discrete';
export { CategoricalColorScheme } from './colorSchemeMap';
// Quantitative Scale Factories
export const linear = (name) => new LinearScale().name(name);
export const log = (name) => new LogScale().name(name);
export const pow = (name) => new PowScale().name(name);
export const sequential = (name) => new SequantialScale().name(name);
export const sqrt = (name) => new SqrtScale().name(name);
export const time = (name) => new TimeScale().name(name);
export const utc = (name) => new UtcScale().name(name);
// Discrete Scale Factories
export const band = (name, bandwidth) => new BandScale().name(name).bandwidthName(bandwidth);
export const ordinal = (name) => new OrdinalScale().name(name);
export const point = (name, step) => new PointScale().name(name).stepName(step);
