export function captureCommonEncodings(props) {
    const result = {};
    const transferProp = (name) => {
        const propVal = props[name];
        if (propVal !== undefined) {
            result[name] = propVal;
        }
    };
    [
        'x',
        'x2',
        'xc',
        'width',
        'y',
        'y2',
        'yc',
        'height',
        'opacity',
        'fill',
        'fillOpacity',
        'stroke',
        'strokeOpacity',
        'strokeWidth',
        'strokeCap',
        'strokeDash',
        'strokeDashOffset',
        'strokeJoin',
        'strokeMiterLimit',
        'cursor',
        'href',
        'tooltip',
        'zIndex',
    ].map(pv => transferProp(pv));
    return result;
}
