/**
 * The scene frame is analagous to a stack-frame - it contains contextual
 * information for a section of a scene, including dimensions, scales, data, and event handlers
 */
export class SceneFrame {
    constructor(node, mark, data, view, viewTL = [0, 0], viewBR = [view.height, view.width], scales = {}, channels = {}, channelId = 0, channelHandlers = {}, boundDataItem = {}) {
        this.node = node;
        this.mark = mark;
        this.data = data;
        this.view = view;
        this.viewTL = viewTL;
        this.viewBR = viewBR;
        this.scales = scales;
        this.channels = channels;
        this.channelId = channelId;
        this.channelHandlers = channelHandlers;
        this.boundDataItem = boundDataItem;
    }
    /**
     * Emits a new scene frame with the given node. Recomputes scales and registers handelrs.
     */
    pushNode(node) {
        const scales = this.getRecomputedScales(node, this.viewTL, this.viewBR);
        return new SceneFrame(node, undefined, this.data, this.view, this.viewTL, this.viewBR, scales, {}, this.channelId, this.channelHandlers, this.boundDataItem);
    }
    pushMark(mark) {
        const channels = this.registerChannels(mark.channels);
        return new SceneFrame(this.node, mark, this.data, this.view, this.viewTL, this.viewBR, this.scales, channels, this.channelId, this.channelHandlers, this.boundDataItem);
    }
    pushData(data) {
        const dataFrame = Object.assign({}, this.data, data);
        return new SceneFrame(this.node, this.mark, dataFrame, this.view, this.viewTL, this.viewBR, this.scales, this.channels, this.channelId, this.channelHandlers, this.boundDataItem);
    }
    /**
     * Pushes a new sceneframe with an updated viewspace. Recomputes scales.
     *
     * @param view The new view to push. This is the size of the group that will be inserted
     * @param viewTL The top-left of the draw-area within the view. This does not affect the emitted
     * group, but does affect draw-scales
     * @param viewBR The bottom-right of the draw-area within the view. This does not affect the emitted
     * group, but does affect draw-scales
     */
    pushView(view, viewTL = [0, 0], viewBR = [view.height, view.width]) {
        const scales = this.getRecomputedScales(this.node, viewTL, viewBR);
        return new SceneFrame(this.node, this.mark, this.data, view, viewTL, viewBR, scales, this.channels, this.channelId, this.channelHandlers, this.boundDataItem);
    }
    pushBoundDataItem(dataItem) {
        return new SceneFrame(this.node, this.mark, this.data, this.view, this.viewTL, this.viewBR, this.scales, this.channels, this.channelId, this.channelHandlers, dataItem);
    }
    getRecomputedScales(node, viewTL, viewBR) {
        const data = this.data;
        let scales = Object.assign({}, this.scales);
        const xBounds = [viewTL[1], viewBR[1]];
        const yBounds = [viewBR[0], viewTL[0]];
        node.scales.forEach(creator => {
            const args = {
                viewBounds: { x: xBounds, y: yBounds },
                data,
                scales,
            };
            const newScales = creator(args);
            scales = Object.assign({}, scales, newScales);
        });
        return scales;
    }
    registerChannels(channels) {
        // For each channel the client specifies, encode the name-mapping in the Scenegraph and
        // map the handler function in our scene result
        return Object.entries(channels).reduce((prev, [eventName, handler]) => {
            prev[eventName] = this.registerHandler(handler);
            return prev;
        }, {});
    }
    registerHandler(handler) {
        const id = `evt${this.channelId++}`;
        this.channelHandlers[id] = handler;
        return id;
    }
}
