import { DEFAULT_HEIGHT, DEFAULT_WIDTH, } from '@markable/interfaces';
import { renderMark } from './element_renderers';
import { translate } from './element_renderers/interfaces';
const DEFAULT_BG_COLOR = 'transparent';
const DEFAULT_ORIGIN = [0, 0];
/**
 * The VirtualSvgRender renders Scenegraph data into a serializable Virtual DOM.
 *
 * The Virtual Dom acts as an intermediate form that can be rendered out into many forms.
 */
export class VirtualSvgConverter {
    render(mark, options) {
        const { width = DEFAULT_WIDTH, height = DEFAULT_HEIGHT, backgroundColor = DEFAULT_BG_COLOR, origin = DEFAULT_ORIGIN, } = options;
        const [x = 0, y = 0] = origin;
        // Create the rendering Context
        let id = 0;
        const context = {
            nextId: () => `${++id}`,
        };
        // Get the rendering of the root mark
        const root = renderMark(mark, context);
        const children = [
            {
                type: 'defs',
                children: root.defs,
            },
            {
                type: 'g',
                transforms: [translate(x, y)],
                children: root.nodes,
            },
        ];
        // Wrap the rendered root in an SVG
        const svg = {
            type: 'svg',
            attrs: {
                width: width + x,
                height: height + y,
            },
            style: {
                backgroundColor,
            },
            children,
        };
        return svg;
    }
}
